<template>
  <div class="scorll-wrapper">
    <div class="head">
      <div class="left">
        <span class="title">{{ title }}</span>
      </div>
      <div class="right">
        <span>更多</span>
        <i class="iconfont icon-youjiantou"></i>
      </div>
    </div>
    <div class="list">
      <div class="iconleft" @click="reduce">
        <i class="iconfont icon-zuojiantou"></i>
      </div>
      <div
        class="item"
        v-for="alItem in resList"
        :key="alItem.actid"
        @click="gotoDeatil(alItem.album_mid)"
      >
        <div class="img">
          <img :src="alItem.buypage" />
        </div>
        <div class="tag">
          <p style="font-size:18px">{{ alItem.album_name }}</p>
          <p>{{ alItem.intro }}</p>
          <p>￥{{ alItem.price }}</p>
        </div>
        <van-button type="default" round class="buy">立即购买</van-button>
      </div>

      <div class="iconright" @click="add">
        <i class="iconfont icon-youjiantou"></i>
      </div>
    </div>
  </div>
</template>

<script>
import {  reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
export default {
  props:{
    albumlist:Array,
    title:String,
  },
  setup(props){
    const state = reactive({
      start:0,
      end:4
    })
    const reduce = ()=>{
      if(state.start>0){
        state.start--
        state.end--
      }
      
    }
    const add = ()=>{
      if(state.end<props.albumlist.length)
      state.start++
      state.end++
    }
    const router = useRouter()
    const gotoDeatil = (id)=>{
      router.push({path:'/cdListPage',query:{id:id}})
  
    }
    return {
      reduce,
      add,
      gotoDeatil,
      ...toRefs(state)
    }
  },
  computed:{
    resList(){
      return this.albumlist.slice(this.start,this.end)
    }
  }
}
</script>

<style lang="less" scoped>
.scorll-wrapper {
  .head {
    display: flex;
    justify-content: space-between;

    .left {
      .title {
        font-size: 25px;
        vertical-align: middle;
      }
      .desc {
        margin-left: 15px;
        font-size: 14px;
        vertical-align: middle;
        color: #969799;
      }
    }
  }
  .list {
    display: flex;
    margin-left: -23px;
    position: relative;
    margin-top: 20px;
    .iconleft,
    .iconright {
      cursor: pointer;
      position: absolute;
      display: none;
      i {
        font-size: 65px;
        font-weight: lighter;
        color: #1ecd9b;
      }
      i:hover {
        color: #1ecd9b;
      }
    }
    .iconleft {
      left: -31px;
      top: 70px;
    }
    .iconright {
      right: -40px;
      top: 70px;
    }
    .item {
      cursor: pointer;
      .img {
        position: relative;
        width: 200px;
        height: 200px;
        overflow: hidden;
        border-radius: 10px;
        .icon-bofang {
          font-size: 65px;
          position: absolute;
          font-size: 65px;
          left: 65px;
          top: 69px;
          display: none;
        }
        img {
          height: 100%;
          width: 100%;
        }
      }
      .img:hover {
        img {
          filter: brightness(0.5);
          transform: scale(1.2);
          transition: all 0.5s;
        }
        .icon-bofang {
          display: block;
        }
      }
      .tag {
        margin-top: 10px;
        p{
          width: 219px;
          font-size: 10px;
          font-size: 13px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      .tag:hover {
        color: #1ecd9b;
      }
      .buy:hover{
        background-color: #f2f3f5;
      }
    }

    .item:not(:first-child) {
      margin-left: 23px;
    }
   
  }
  .list:hover .iconleft,
  .list:hover .iconright {
    display: block;
  }
}
</style>