<template>
  <div class="cd-wrapper">
    <div class="head">
      <mySwipe :swipe="swipe" class="swipe" />
    </div>
    <div class="content" v-if="content.length">
      <cdList
        :albumlist="content[0].albumlist"
        title="最新上架"
        class="cdlist"
      />
      <cdList
        :albumlist="content[1].albumlist"
        title="本周最热"
        class="cdlist"
      />
      <cdList
        :albumlist="content[2].albumlist"
        :title="content[2].title"
        class="cdlist"
      />
      <cdList
        :albumlist="content[3].albumlist"
        :title="content[3].title"
        class="cdlist"
      />
      <cdList
        :albumlist="content[4].albumlist"
        :title="content[4].title"
        class="cdlist"
      />
      <cdList
        :albumlist="content[5].albumlist"
        :title="content[5].title"
        class="cdlist"
      />
      <cdList
        :albumlist="content[6].albumlist"
        :title="content[6].title"
        class="cdlist"
      />
      <cdList
        :albumlist="content[7].albumlist"
        :title="content[7].title"
        class="cdlist"
      />
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, toRefs } from "vue";
import axios from "axios";
import mySwipe from "../components/mySwipe.vue";
import cdList from "../components/cdList.vue";
export default {
  components: {
    mySwipe,
    cdList,
  },
  setup() {
    const state = reactive({
      banner: [],
      swipe: [],
      content: [],
      start: 0,
      end: 4,
    });

    onMounted(() => {
      axios({
        url: "/api/getDigitalAlbumLists",
      }).then((res) => {
        console.log(res.data.response.data);
        state.banner = res.data.response.data.banner;
        state.content = res.data.response.data.content;
        state.swipe = state.banner.map((el) => {
          let obj = {};
          obj.pic_info = {};
          obj.pic_info.url = el.picurl;
          obj.jump_info = {}
          let l = (el.jumpurl).indexOf('=')
          let r = (el.jumpurl).indexOf('&')
          obj.jump_info.url = (el.jumpurl).slice(l+1,r)
          
          return obj;
        });
        for (let i = 0; i < state.content.length; i++) {
          for (let j = 0; j < state.content[i].albumlist.length; j++) {
            let l = state.content[i].albumlist[j].buypage.indexOf("=");
            let r = state.content[i].albumlist[j].buypage.indexOf("&");
            state.content[i].albumlist[j].buypage =
              "https://y.qq.com/music/photo_new/T002R300x300M000" +
              state.content[i].albumlist[j].buypage.slice(l + 1, r) +
              ".jpg?max_age=2592000";
          }
        }
      });
    });
    return {
      ...toRefs(state),
    };
  },
};
</script>

<style lang="less" scoped>
.cd-wrapper {
  .head {
    margin-top: 35px;
    .swipe {
      margin-left: 33px;
    }
  }
  .content {
    .cdlist {
      margin-top: 40px;
    }
  }
}
</style>